.menu {
	height: auto;
	background-color: $rose;
}
.menu__one {
    height: auto;
    @include medium {
        flex-direction: row;
        height:50vh;
    }
    
}
.menu__photo {
	position: relative;
	height: 50vh;	
	background-image: url(../img/shoes_jump.jpg);
	background-size: cover;
	background-position: center;

    @include medium {
        width: 60vw;
        height: auto;
    }

    p {
        text-align: justify;
        color: $white;
        padding: 30px;
        
        @include small {
            width:60vw;
            position: absolute;
            right: 0px;
        }
        @include medium {
            display: none;
        }
    }
    a {
        position: absolute;
        bottom: 30px;
        display: flex;
        align-items: center;
        background-color: $white;

        &:nth-child(3) {
            right: 30px;
        }
        &:nth-child(2){
            left: 30px;
        }
    }
}
.menu__article {
	background-color: $grey;
	padding: 15px;
	border-radius: 5px;
	margin: 7vw;

    @include small {
        margin: 3vw;
    }
    @include medium {
        display: flex;
        flex-direction: column;
        justify-content: space-around;
        align-items: center;
        position: relative;
    }
    p {
        color: white;
        text-align: center;
    }
}
.menu__info {
    display: none;
    
    @include medium{
        width: 40vw;
        display: flex;
        flex-direction: column;
    }
    p {
        text-align: justify;
        padding: 10px 0px;

    }
}
a {
    padding: 10px;
    border-radius: 5px;
    img {
        height: 5vw;
        max-height: 40px;
    }
}
.menu article:first-of-type h1{
	font-family: 'Bungee Shade', cursive;
	font-size: 2rem;
    text-align: center;
    color: white;
}
.menu__article>a{
	background-color: black;
    color: ghostwhite;
    position: absolute;
    right: 30px;
    font-size: 1rem;
    padding: 5px;
    margin-top: 5px;
    @include medium {
        bottom: 0px;
    }
}

.menu__parallax{
	width: 100%;
	height: 400px;
	background: url('../img/nike_text_mobile.jpg');
	background-size: cover;
	background-attachment: fixed;
    background-position: center;

    @include medium {
        background: url(../img/nike_text.jpg);
        background-size: cover;
        background-attachment: fixed;
        background-position: center;
    }
}
.menu__produits {
	display: flex;
	flex-direction: column;
	width: 100vw;

    h2 {
        padding: 20px;
        background-color: $white;
        color: $black;
        text-decoration: underline;

        font-size: 4vh;
        text-align: center;
    }
    img{
        width: 50%;
        height: 20%;
        
        @include small {
            width:25%;
        }
    }
}
.menu__photos {
	display: flex;
	flex-wrap: wrap;
}
.menu__app {
    display:flex;
    height: 70vh;
}
.app__photo {
	width: 100%;
    background: url(../img/running_app.jpg);
    background-position: 50% 50%;
    background-size: cover;
}
.app__cases {
    display: none;
    flex-direction: column;
    justify-content: space-between;
    width: 20vw;
    height: 70vh;
    @include medium{
        display: flex;
    }
    img {
        width: 100%;
        height: 33%;
    }
    div:nth-child(2){
            padding: 20px;
    }


}










